.flexRow {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  border: 1px solid #25a69a;
  border-radius: 5px;
  padding: 0.75rem;
  margin: 1.5rem;
}

.input {
  margin: 1rem;
}

.title {
  text-align: center;
}

.colorBar {
  padding: 0.75rem;
  height: 1rem;
}
